import { HTMLAttributes, useContext } from "react";
import cx from "ts-classnames";

import ModalContext from "./ModalContext";

type Props = HTMLAttributes<HTMLDivElement>;

export default function ModalHeader({ children }: Props) {
  const { onClose } = useContext(ModalContext);

  return (
    <div
      className={cx("flex", "justify-between", "items-center", "p-5", "rounded-t", "border-b", "dark:border-gray-600")}
    >
      {children}

      {onClose && (
        <button
          type="button"
          className={cx(
            "text-gray-400",
            "bg-transparent",
            "hover:bg-gray-200",
            "hover:text-gray-900",
            "rounded-lg",
            "text-sm",
            "p-1.5",
            "ml-auto",
            "inline-flex",
            "items-center",
            "dark:hover:bg-gray-600",
            "dark:hover:text-gray-200"
          )}
          onClick={() => onClose()}
        >
          <svg className={cx("w-5", "h-5")} fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
            <path
              fillRule="evenodd"
              clipRule="evenodd"
              d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z"
            ></path>
          </svg>
        </button>
      )}
    </div>
  );
}
